	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>Talk of work or something</strong></p>\n<p>It seems that the trial version Flash made by Uchi (see the diary of the other day for details) was released on the official website. Well I strongly would like to make a loading screen ... .... (There are about 8 MB) But <a href=\"http://www.adobe.com/jp/products/flex/\">Flex is</a> interesting. I tried Flex for the first time with this trial version Flash, but it completely caught me. If it is said that it is possible to make Flash application using Flash in ActionScript validation, it will be quite handy, but if it is Flex, it is a form to make it from the main loop on its own, so it will be easy to do it. In other words, as if to make a Java applet as well. IDE is also easy to familiar on Eclipse base. (It took a while to get used to the CodeWarrior used to make DS application) It took a while to get used to the language specification of ActionScript 3.0, but it was still easier than FlashLite 1.1, PHP and Objective-C Hey. (PHP is terribly useful for making small but it does not make me chewy to make a decouch.Objective-C ... ... I do not want to hear it) Next time <a href=\"http://slashdot.jp/developers/article.pl?sid=08/11/23/0717231\">I use C / ++ instead of ActionScript to use a Flash application Can you make it?</a> This is forced to wktk.</p>\n<h2>Winter comic stuff</h2>\n<p>I was wondering if I would like to release a trial version of \"thM (tentative name)\" work co-produced with Mr. <a href=\"http://dangoya.moo.jp/\">DANGOYA</a> , but because it was postponed due to various circumstances, <a href=\"/!/thA/\">We</a> will distribute trial version of <a href=\"/!/thA/\">Tokijo castle</a> remake. (No, perfect implementation is not going to make it in time for a moment orz) We will add an app for free to those who purchased Tokijo Castle (400 yen). In addition, those who already have the sealed castle will receive a free shipping if you bring a Jacket seal jacket. When it is already published as sealed castle (CD-R version), dream last movie (few copies so early), Leila, danmaq soundtrack.</p>\n<h2>update information</h2>\n<p><a href=\"/tag/1123\">One SS is released</a> . VOCALOID stuff (Master x Miku), non ero / serious one.</p>","frontmatter":{"cover":null,"date":"2008-11-25T00:00:00+09:00","strDate":"2008/11/24","title":"The · company · hickey"}}},"pathContext":{"path":"/en/blog/2008/11/25-the・会社ヒッキー/","langKey":"en"}}

